// wbg

.wbg {
  overflow: hidden;
  position: fixed;
  height: 100%;
  width: 100%;
  visibility: visible; // fix for safari
  z-index: -1;
  // opacity: 0.5;
}
.first_bg {
  position: absolute;
  right: 25%;
  @include media("<mob") {
    right: 35%;
    top: -3%;
  }
}
.second_bg {
  position: absolute;
  bottom: -2%;
  left: 2.6%;
  @include media("<mob") {
    display: none;
  }
}
.third_bg {
  position: absolute;
  bottom: 0;
  right: 0;
  @include media("<mob") {
    bottom: -25%;
  }
}