/* reset */
*, *::after, *::before {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
}

body, html {
  height: 100%;
  width: 100%;
}

form {
  margin: 0;
  padding: 0;
}

input, input:focus, textarea, select {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
  outline: none !important;
}

button,
textarea,
.select select,
input[type="text"],
input[type="email"],
input[type="search"],
input[type="submit"],
input[type="button"],
input[type="reset"],
input[type="number"],
input[type="range"] {
